"use server";

import { generateText } from "ai";
import { google } from "@ai-sdk/google";
import { db } from "@/firebase/admin";

export async function startAxonSession(params: StartAxonSessionParams) {
  const { userId, jobRole, experience, sessionType } = params;

  try {
    // Generate adaptive questions based on role and experience
    const { text: questionsText } = await generateText({
      model: google("gemini-2.0-flash-001"),
      prompt: `Generate 5 interview questions for a ${experience} level ${jobRole} position. 
      
      Requirements:
      - Start with easier questions and progressively increase difficulty
      - Include behavioral, technical, and situational questions
      - Format as JSON array of strings
      - Questions should be realistic and commonly asked
      
      Example format: ["Tell me about yourself", "Describe a challenging project you worked on"]`,
      system: "You are an expert interviewer. Generate realistic, progressive interview questions."
    });

    let questions: string[];
    try {
      questions = JSON.parse(questionsText);
    } catch {
      // Fallback questions if JSON parsing fails
      questions = [
        "Tell me about yourself and your background.",
        "Why are you interested in this role?",
        "Describe a challenging project you've worked on.",
        "How do you handle working under pressure?",
        "Where do you see yourself in 5 years?"
      ];
    }

    const sessionId = `axon_${Date.now()}_${userId}`;
    const sessionData = {
      sessionId,
      userId,
      jobRole,
      experience,
      sessionType,
      questions,
      currentQuestionIndex: 0,
      startTime: new Date(),
      status: "active",
      responses: []
    };

    // Store session in Firestore
    await db.collection("axon_sessions").doc(sessionId).set(sessionData);

    return {
      success: true,
      session: {
        sessionId,
        currentQuestion: questions[0],
        questionIndex: 0,
        totalQuestions: questions.length,
        startTime: new Date().toISOString()
      }
    };
  } catch (error) {
    console.error("Error starting Axon session:", error);
    return { success: false, error: "Failed to start session" };
  }
}

export async function getInstantFeedback(params: GetInstantFeedbackParams) {
  const { sessionId, questionIndex, textAnswer, audioBlob, userId } = params;

  try {
    // Get session data
    const sessionDoc = await db.collection("axon_sessions").doc(sessionId).get();
    if (!sessionDoc.exists) {
      return { success: false, error: "Session not found" };
    }

    const sessionData = sessionDoc.data();
    const currentQuestion = sessionData?.questions[questionIndex];

    // Analyze the response using AI
    const { text: feedbackText } = await generateText({
      model: google("gemini-2.0-flash-001"),
      prompt: `Question: "${currentQuestion}"
Candidate's Answer: "${textAnswer}"
Job Role: ${sessionData?.jobRole || 'General'}
Experience Level: ${sessionData?.experience || 'General'}

Analyze this response and provide personalized feedback in this JSON format:
{
  "confidence": [score 1-100],
  "clarity": [score 1-100],
  "pace": [score 1-100],
  "keyPoints": ["specific strengths, skills, or examples mentioned"],
  "suggestions": ["concrete improvements based on missing details or weaknesses"]
}

Focus on:
- Identifying relevant keywords, skills, and concepts mentioned
- Highlighting specific strengths from their actual words
- Pointing out missing details or vague explanations
- Suggesting concrete improvements (metrics, examples, technical depth)
- Adapting feedback to the job role and experience level
- If answer is too vague, explain why and suggest specific improvements`,
      system: "You are an AI interviewer giving feedback on candidate responses. Your task is to carefully read each answer, identify relevant keywords, skills, and concepts, and generate personalized feedback that: 1. Highlights strengths (specific skills, examples, or experiences mentioned). 2. Points out weaknesses or missing details (e.g., lack of examples, vague explanation). 3. Suggests concrete improvements (e.g., 'you could include metrics,' 'try mentioning teamwork examples,' 'expand on technical depth'). 4. Adapts feedback to the job title, level of experience, and question asked. 5. Avoids repeating the same generic response for all answers. Always ground your feedback in the candidate's actual words. If no relevant keywords are found, politely explain that the answer was too vague and suggest ways to improve it. Keep the tone professional, supportive, and constructive."
    });

    let feedback;
    try {
      feedback = JSON.parse(feedbackText);
    } catch {
      // Fallback feedback
      feedback = {
        confidence: 75,
        clarity: 80,
        pace: 70,
        keyPoints: ["Good structure", "Relevant examples"],
        suggestions: ["Add more specific details", "Practice speaking more confidently"]
      };
    }

    // Store the response and feedback
    const responseData = {
      questionIndex,
      question: currentQuestion,
      answer: textAnswer,
      feedback,
      timestamp: new Date(),
      hasAudio: !!audioBlob
    };

    await db.collection("axon_sessions").doc(sessionId).update({
      [`responses.${questionIndex}`]: responseData
    });

    return { success: true, feedback };
  } catch (error) {
    console.error("Error getting instant feedback:", error);
    return { success: false, error: "Failed to analyze response" };
  }
}

export async function getNextQuestion(params: GetNextQuestionParams) {
  const { sessionId, currentIndex } = params;

  try {
    const sessionDoc = await db.collection("axon_sessions").doc(sessionId).get();
    if (!sessionDoc.exists) {
      return { success: false, error: "Session not found" };
    }

    const sessionData = sessionDoc.data();
    const questions = sessionData?.questions || [];
    const nextIndex = currentIndex + 1;

    if (nextIndex >= questions.length) {
      return { success: false, error: "No more questions", isComplete: true };
    }

    // Update session with new question index
    await db.collection("axon_sessions").doc(sessionId).update({
      currentQuestionIndex: nextIndex
    });

    return {
      success: true,
      question: questions[nextIndex],
      questionIndex: nextIndex,
      isComplete: false
    };
  } catch (error) {
    console.error("Error getting next question:", error);
    return { success: false, error: "Failed to get next question" };
  }
}

export async function endAxonSession(params: EndAxonSessionParams) {
  const { sessionId, userId } = params;

  try {
    const sessionDoc = await db.collection("axon_sessions").doc(sessionId).get();
    if (!sessionDoc.exists) {
      return { success: false, error: "Session not found" };
    }

    const sessionData = sessionDoc.data();
    const responses = sessionData?.responses || [];

    // Generate session summary
    const { text: summaryText } = await generateText({
      model: google("gemini-2.0-flash-001"),
      prompt: `Generate a session summary based on these interview responses:

${JSON.stringify(responses, null, 2)}

Provide a summary in this JSON format:
{
  "overallScore": [average score 1-100],
  "strengths": ["strength1", "strength2", "strength3"],
  "areasForImprovement": ["area1", "area2"],
  "keyInsights": ["insight1", "insight2"],
  "nextSteps": ["step1", "step2"]
}`,
      system: "You are an expert interview coach providing comprehensive session analysis."
    });

    let summary;
    try {
      summary = JSON.parse(summaryText);
    } catch {
      summary = {
        overallScore: 75,
        strengths: ["Good communication", "Relevant examples"],
        areasForImprovement: ["More confidence", "Better structure"],
        keyInsights: ["Shows potential", "Needs practice"],
        nextSteps: ["Practice more", "Work on confidence"]
      };
    }

    // Update session with summary and end time
    await db.collection("axon_sessions").doc(sessionId).update({
      status: "completed",
      endTime: new Date(),
      summary
    });

    return { success: true, summary };
  } catch (error) {
    console.error("Error ending Axon session:", error);
    return { success: false, error: "Failed to end session" };
  }
}

export async function getAxonSessionHistory(params: GetAxonSessionHistoryParams) {
  const { userId, limit = 10 } = params;

  try {
    const sessionsQuery = await db
      .collection("axon_sessions")
      .where("userId", "==", userId)
      .where("status", "==", "completed")
      .orderBy("endTime", "desc")
      .limit(limit)
      .get();

    const sessions = sessionsQuery.docs.map(doc => {
      const data = doc.data();
      return {
        id: doc.id,
        ...data,
        startTime: data.startTime?.toDate?.() || data.startTime,
        endTime: data.endTime?.toDate?.() || data.endTime,
        responses: data.responses?.map((response: any) => ({
          ...response,
          timestamp: response.timestamp?.toDate?.() || response.timestamp
        })) || []
      };
    });

    return { success: true, sessions };
  } catch (error) {
    console.error("Error getting session history:", error);
    return { success: false, error: "Failed to get session history" };
  }
}